// client/RoomClient.js (ÏµúÏ¢Ö ÏôÑÏÑ± Î≤ÑÏ†Ñ)
import { EventEmitter } from "./utils/EventEmitter.js";

export class RoomClient extends EventEmitter {
  constructor(uiManager) {
    super();
    this.uiManager = uiManager;

    this.ws = null;
    this.device = null;
    this.sendTransport = null;
    this.recvTransport = null;
    this.localStream = null;
    this.producers = new Map();
    this.consumers = new Map();
    this.producerIdToConsumer = new Map(); // ‚úÖ producerId -> consumer Îßµ
    this.actionCallbackMap = new Map();
    this.pendingConsumeList = [];
    this.isAdmin = false; // ‚úÖ Í¥ÄÎ¶¨Ïûê Ïó¨Î∂Ä
    this.screenProducer = null; // ‚úÖ ÌôîÎ©¥ Í≥µÏú† ÌîÑÎ°úÎìÄÏÑú
  }

  join(roomId) {
    // ‚úÖ roomIdÎ•º Ïù∏ÏûêÎ°ú Î∞õÏäµÎãàÎã§.
    if (!roomId) {
      throw new Error("roomId is required to join a room");
    }
    // ‚úÖ WebSocket Ï†ëÏÜç Ï£ºÏÜåÏóê roomIdÎ•º ÏøºÎ¶¨ ÌååÎùºÎØ∏ÌÑ∞Î°ú Ï∂îÍ∞ÄÌï©ÎãàÎã§.
    this.ws = new WebSocket(`wss://192.168.5.133:3000/?roomId=${roomId}`);

    this.ws.onopen = () => {
      console.log("‚úÖ WebSocket connected");
      try {
        this.device = new window.mediasoupClient.Device();
        this.ws.send(JSON.stringify({ action: "getRtpCapabilities" }));
      } catch (err) {
        console.error("‚ùå Device creation failed:", err);
      }
    };

    this.ws.onmessage = async (event) => {
      const msg = JSON.parse(event.data);
      console.log("üì© Received:", msg);

      const cb = this.actionCallbackMap.get(msg.action);
      if (cb) {
        cb(msg);
        this.actionCallbackMap.delete(msg.action);
        return;
      }

      switch (msg.action) {
        case "adminInfo":
          this.isAdmin = msg.data.isAdmin;
          this.emit("adminStatus", this.isAdmin); // UI Îß§ÎãàÏ†ÄÏóêÍ≤å ÏïåÎ¶º
          break;
        case "rtpCapabilities":
          await this._handleRtpCapabilities(msg.data);
          break;
        case "createTransportResponse":
          await this._handleCreateTransportResponse(msg.data);
          break;
        case "createConsumerTransportResponse":
          await this._handleCreateConsumerTransportResponse(msg.data);
          break;
        case "existingProducers":
          await this._handleExistingProducers(msg.data);
          break;
        case "newProducerAvailable":
          await this._handleNewProducerAvailable(msg);
          break;
        // case "consumeResponse":
        //   await this._handleConsumeResponse(msg.data);
        //   break;
        case "producerClosed":
          this._handleProducerClosed(msg);
          break;
      }
    };
  }

  sendPeerStatus(statusData) {
    if (this.ws && this.ws.readyState === WebSocket.OPEN) {
      this.ws.send(
        JSON.stringify({
          action: "updatePeerStatus",
          data: statusData,
        })
      );
    }
  }

  _waitForAction(actionName, callback) {
    this.actionCallbackMap.set(actionName, callback);
  }

  async _handleRtpCapabilities(data) {
    try {
      await this.device.load({ routerRtpCapabilities: data });
      console.log("‚úÖ Device loaded successfully");
      this.ws.send(JSON.stringify({ action: "createTransport" }));
    } catch (err) {
      console.error("‚ùå Failed to load device capabilities:", err);
    }
  }

  async _handleCreateTransportResponse(data) {
    this.sendTransport = this.device.createSendTransport(data);

    this.sendTransport.on(
      "connect",
      ({ dtlsParameters }, callback, errback) => {
        this.ws.send(
          JSON.stringify({
            action: "connectTransport",
            data: { dtlsParameters },
          })
        );
        this._waitForAction("transportConnected", callback);
      }
    );

    this.sendTransport.on(
      "produce",
      async ({ kind, rtpParameters, appData }, callback, errback) => {
        try {
          console.log(`üé¨ Producing ${kind}...`);
          // _sendRequestÎ•º ÏÇ¨Ïö©ÌïòÏó¨ ÏÑúÎ≤ÑÏóê produce ÏöîÏ≤≠ÏùÑ Î≥¥ÎÉÖÎãàÎã§.
          const producer = await this._sendRequest("produce", {
            kind,
            rtpParameters,
            appData,
          });
          console.log(
            `‚úÖ ${kind} production started with server id: ${producer.id}`
          );
          this.producers.set(producer.id, producer); // Ïã§Ï†ú producer Í∞ùÏ≤¥ Ï†ÄÏû•
          callback({ id: producer.id });
        } catch (error) {
          errback(error);
        }
      }
    );

    await this._startProducing();
  }

  async _startProducing() {
    try {
      this.localStream = await navigator.mediaDevices.getUserMedia({
        video: { width: { ideal: 640 }, height: { ideal: 480 } },
        audio: true,
      });
      const videoElement = document.getElementById("localVideo");
      videoElement.srcObject = this.localStream;

      videoElement.oncanplay = () => {
        videoElement.oncanplay = null;
        console.log("‚úÖ Video element is ready to play.");
        this.emit("localStreamReady", videoElement); // AI Î™®ÎìàÏù¥ videoElementÎ•º Ï∞∏Ï°∞Ìï† Ïàò ÏûàÎèÑÎ°ù Ï†ÑÎã¨

        (async () => {
          const videoTrack = this.localStream.getVideoTracks()[0];
          const audioTrack = this.localStream.getAudioTracks()[0];
          let videoProducer, audioProducer;

          if (videoTrack) {
            videoProducer = await this.sendTransport.produce({
              track: videoTrack,
            });
            this.producers.set(videoProducer.id, videoProducer); // ÌîÑÎ°úÎìÄÏÑú Í∞ùÏ≤¥ Ï†ÄÏû•
          }
          if (audioTrack) {
            audioProducer = await this.sendTransport.produce({
              track: audioTrack,
            });
            this.producers.set(audioProducer.id, audioProducer); // ÌîÑÎ°úÎìÄÏÑú Í∞ùÏ≤¥ Ï†ÄÏû•
          }
          this.ws.send(JSON.stringify({ action: "deviceReady" }));
          // ‚úÖ [ÌïµÏã¨ Ï∂îÍ∞Ä] Î™®Îì† produceÍ∞Ä ÎÅùÎÇú ÌõÑ, Ïª®Ìä∏Î°§ Ï§ÄÎπÑ ÏôÑÎ£å Ïù¥Î≤§Ìä∏Î•º Î∞©ÏÜ°Ìï©ÎãàÎã§.
          console.log("‚úÖ All producers created. Controls are now ready.");
          this.emit("controlsReady");
        })();
      };
    } catch (err) {
      console.error("‚ùå CRITICAL: Failed to get user media.", err);
      alert(`Ïπ¥Î©îÎùº/ÎßàÏù¥ÌÅ¨Î•º Í∞ÄÏ†∏Ïò¨ Ïàò ÏóÜÏäµÎãàÎã§: ${err.name}`);
    }
  }

  async _handleCreateConsumerTransportResponse(data) {
    this.recvTransport = this.device.createRecvTransport(data);
    this.recvTransport.on("connect", ({ dtlsParameters }, callback) => {
      this.ws.send(
        JSON.stringify({
          action: "connectConsumerTransport",
          data: { dtlsParameters },
        })
      );
      this._waitForAction("consumerTransportConnected", callback);
    });

    // ‚úÖ recvTransportÍ∞Ä Ï§ÄÎπÑÎêòÏóàÏúºÎØÄÎ°ú, ÎåÄÍ∏∞ Ï§ëÏù∏ Î™®Îì† consumerÎ•º Ï≤òÎ¶¨Ìï©ÎãàÎã§.
    const pendingConsumes = [...this.pendingConsumeList];
    this.pendingConsumeList = [];
    console.log(
      `‚úÖ RecvTransport ready. Processing ${pendingConsumes.length} pending consumers.`
    );
    for (const consumeData of pendingConsumes) {
      await this._consume(consumeData);
    }
  }

  async _handleExistingProducers(producers) {
    console.log(`üìã Found ${producers.length} existing producers.`);
    for (const producer of producers) {
      this.pendingConsumeList.push(producer);
    }

    // ‚úÖ recvTransportÍ∞Ä ÏïÑÏßÅ ÏóÜÏúºÎ©¥ ÏÉùÏÑ±ÏùÑ ÏöîÏ≤≠ÌïòÍ≥†,
    //    Ïù¥ÎØ∏ ÏûàÎã§Î©¥ Î∞îÎ°ú ÎåÄÍ∏∞Ïó¥ÏùÑ Ï≤òÎ¶¨ÌïòÏó¨ ÌÉÄÏù¥Î∞ç Î¨∏Ï†úÎ•º Ìï¥Í≤∞Ìï©ÎãàÎã§.
    if (!this.recvTransport) {
      this.ws.send(JSON.stringify({ action: "createConsumerTransport" }));
    } else {
      const pendingConsumes = [...this.pendingConsumeList];
      this.pendingConsumeList = [];
      for (const consumeData of pendingConsumes) {
        await this._consume(consumeData);
      }
    }
  }

  async _handleNewProducerAvailable(producerInfo) {
    console.log("üÜï A new producer is available.", producerInfo);
    const { producerId, kind, appData } = producerInfo;
    const consumeData = { producerId, kind, appData }; // appDataÎèÑ Ï†ÑÎã¨

    // ‚úÖ recvTransportÍ∞Ä ÏóÜÏúºÎ©¥ ÎåÄÍ∏∞Ïó¥Ïóê Ï∂îÍ∞ÄÌïòÍ≥†, ÏûàÏúºÎ©¥ Î∞îÎ°ú consumeÏùÑ ÏãúÎèÑÌï©ÎãàÎã§.
    if (!this.recvTransport) {
      this.pendingConsumeList.push(consumeData);
    } else {
      await this._consume(consumeData);
    }
  }

  async _consume({ producerId, kind, appData }) {
    // ‚úÖ Ï§ëÎ≥µ consumer ÏÉùÏÑ±ÏùÑ Î∞©ÏßÄÌïòÎäî Í∞ÄÎìú
    if (this.producerIdToConsumer.has(producerId)) {
      console.warn(
        `Consumer for producer ${producerId} already exists. Skipping.`
      );
      return;
    }

    console.log(`üì° Requesting to consume producer ${producerId}`);
    if (!this.recvTransport) {
      console.warn("recvTransport is not ready, queuing consume request");
      this.pendingConsumeList.push({ producerId, kind });
      return;
    }
    try {
      const data = await this._sendRequest("consume", {
        rtpCapabilities: this.device.rtpCapabilities,
        producerId,
        kind,
      });

      const consumer = await this.recvTransport.consume({
        id: data.id,
        producerId: data.producerId,
        kind: data.kind,
        rtpParameters: data.rtpParameters,
        appData: { ...appData }, // ÏÑúÎ≤ÑÏóêÏÑú Î∞õÏùÄ appDataÎ•º consumerÏóê Ï†ÄÏû•
      });
      this.consumers.set(consumer.id, consumer);
      this.producerIdToConsumer.set(producerId, consumer); // ‚úÖ ÏÉà ÎßµÏóê Ï∂îÍ∞Ä

      // UI Îß§ÎãàÏ†ÄÍ∞Ä ÌôîÎ©¥Ïóê Í∑∏Î¶¥ Ïàò ÏûàÎèÑÎ°ù Ïù¥Î≤§Ìä∏Î•º Î∞úÏÉùÏãúÌÇµÎãàÎã§.
      this.emit("new-consumer", consumer);

      // 4. ÏÉùÏÑ±Îêú consumerÎ•º Ï¶âÏãú resumeÌïòÎèÑÎ°ù ÏÑúÎ≤ÑÏóê ÏöîÏ≤≠Ìï©ÎãàÎã§.
      console.log(`üöÄ Resuming consumer ${consumer.id}`);
      this.ws.send(
        JSON.stringify({
          action: "resumeConsumer",
          data: { consumerId: consumer.id },
        })
      );
    } catch (error) {
      console.error(`‚ùå Failed to create consumer for ${producerId}:`, error);
    }
  }

  _handleProducerClosed({ producerId }) {
    console.log(`üö´ Producer ${producerId} closed.`);
    const consumer = this.producerIdToConsumer.get(producerId);
    if (consumer) {
      consumer.close();
      this.consumers.delete(consumer.id);
      this.producerIdToConsumer.delete(producerId);
    }
    this.emit("producer-closed", producerId);
  }

  async _sendRequest(action, data) {
    return new Promise((resolve, reject) => {
      const callbackAction = `${action}Response`;
      this._waitForAction(callbackAction, (response) => {
        if (response.error) {
          reject(new Error(response.error));
        } else {
          resolve(
            response.id ? { id: response.id, ...response.data } : response.data
          );
        }
      });
      this.ws.send(JSON.stringify({ action, data }));
    });
  }
  // ‚úÖ Ïò§ÎîîÏò§ Ìä∏ÎûôÏùÑ ÎÅÑÍ±∞ÎÇò Ïº≠ÎãàÎã§.
  async setAudioEnabled(enabled) {
    const audioProducer = this._findProducerByKind("audio");
    if (!audioProducer) return;

    if (enabled) {
      await audioProducer.resume();
    } else {
      await audioProducer.pause();
    }
    // ÌïÑÏöîÌïòÎã§Î©¥ ÏÑúÎ≤ÑÏóê ÏùåÏÜåÍ±∞ ÏÉÅÌÉúÎ•º ÏïåÎ¶¨Îäî ÏãúÍ∑∏ÎÑêÎßÅÏùÑ Î≥¥ÎÇº Ïàò ÏûàÏäµÎãàÎã§.
    // this.sendPeerStatus({ isMuted: !enabled });
  }

  // ‚úÖ ÎπÑÎîîÏò§ Ìä∏ÎûôÏùÑ ÎÅÑÍ±∞ÎÇò Ïº≠ÎãàÎã§.
  async setVideoEnabled(enabled) {
    const videoProducer = this._findProducerByKind("video");
    if (!videoProducer) return;

    if (enabled) {
      await videoProducer.resume();
    } else {
      await videoProducer.pause();
    }
  }
  _findProducerByKind(kind) {
    // RoomClientÍ∞Ä Í¥ÄÎ¶¨ÌïòÎäî producers ÎßµÏóêÏÑú Ï∞æÏäµÎãàÎã§.
    for (const producer of this.producers.values()) {
      if (producer.kind === kind) {
        return producer;
      }
    }
    return null;
  }

  // ‚úÖ ÌôîÎ©¥ Í≥µÏú† ÏãúÏûë
  async startScreenShare() {
    if (this.screenProducer) {
      console.warn("Screen sharing is already active.");
      return;
    }

    try {
      const stream = await navigator.mediaDevices.getDisplayMedia({
        video: true,
      });
      const track = stream.getVideoTracks()[0];

      this.screenProducer = await this.sendTransport.produce({
        track,
        appData: { source: "screen" },
      });

      // Î∏åÎùºÏö∞Ï†ÄÏùò 'Í≥µÏú† Ï§ëÏßÄ' Î≤ÑÌäº ÌÅ¥Î¶≠ Í∞êÏßÄ
      track.onended = () => {
        console.log("Screen sharing stopped by browser button.");
        this.stopScreenShare();
      };

      this.producers.set(this.screenProducer.id, this.screenProducer);
      this.emit("screenShareState", { isSharing: true });
      this.emit("local-screen-share-started", this.screenProducer.track); // ‚úÖ Î°úÏª¨ UIÎ•º ÏúÑÌïú Ïù¥Î≤§Ìä∏
    } catch (err) {
      console.error("‚ùå Failed to start screen sharing:", err);
    }
  }

  // ‚úÖ ÌôîÎ©¥ Í≥µÏú† Ï§ëÏßÄ
  async stopScreenShare() {
    if (!this.screenProducer) {
      console.warn("No active screen share to stop.");
      return;
    }

    console.log("üöÄ Requesting to stop screen share.");
    // ÏÑúÎ≤ÑÏóê ÌôîÎ©¥ Í≥µÏú† Ï§ëÏßÄÎ•º Î™ÖÏãúÏ†ÅÏúºÎ°ú ÏöîÏ≤≠
    this.ws.send(
      JSON.stringify({
        action: "stopScreenShare",
        data: { producerId: this.screenProducer.id },
      })
    );

    // Î°úÏª¨ ÌîÑÎ°úÎìÄÏÑú Ï†ïÎ¶¨
    const producerId = this.screenProducer.id;
    this.screenProducer.close(); // Ïä§Ìä∏Î¶ºÏùÑ Îã´Í≥† 'close' Ïù¥Î≤§Ìä∏Î•º Î∞úÏÉùÏãúÌÇ¥
    this.producers.delete(producerId);
    this.screenProducer = null;
    this.emit("screenShareState", { isSharing: false });
    this.emit("local-screen-share-stopped"); // ‚úÖ Î°úÏª¨ UI Ï†ïÎ¶¨Î•º ÏúÑÌïú Ïù¥Î≤§Ìä∏
  }
}
