// client/modules/MediaPipeModule.js

import { EventEmitter } from "../utils/EventEmitter.js";

// ‚úÖ EventEmitterÎ•º ÏÉÅÏÜçÎ∞õÏäµÎãàÎã§.
export class MediaPipeModule extends EventEmitter {
  constructor(videoElement) {
    super();

    this.videoElement = videoElement;
    this.worker = new Worker("./dist/mediapipe-worker.bundle.js");

    // ‚úÖ 1. Î™®Îì† ÏÉÅÌÉúÏôÄ ÏÉÅÏàòÎ•º ÌÅ¥ÎûòÏä§Ïùò ÏÜçÏÑ±(this)ÏúºÎ°ú Î≥ÄÍ≤ΩÌï©ÎãàÎã§.
    this.isDrowsy = false;
    this.isPresent = true;

    this.LEFT_EYE = [33, 160, 158, 133, 153, 144];
    this.RIGHT_EYE = [362, 385, 387, 263, 373, 380];
    this.EAR_THRESH = 0.2;
    this.DROWSY_FRAMES = 10;

    this.closureFrames = 0;
    this.absenceCounter = 0;
    this.ABSENCE_CONSECUTIVE_FRAMES = 15; // ÌïÑÏöîÏóê Îî∞Îùº Ï°∞Ï†ï

    this.worker.onerror = (error) => {
      console.error("‚ùå MediaPipe Worker Ïò§Î•ò:", error);
      this.emit("error", error); // ÏóêÎü¨ÎèÑ Ïù¥Î≤§Ìä∏Î°ú Ïô∏Î∂ÄÏóê ÏïåÎ¶ΩÎãàÎã§.
    };

    // ‚úÖ Í≤ΩÏüÅ ÏÉÅÌÉú(Race Condition)Î•º ÌîºÌïòÍ∏∞ ÏúÑÌï¥ onmessage Ìï∏Îì§Îü¨Î•º ÏÉùÏÑ±ÏûêÏóêÏÑú ÏÑ§Ï†ïÌï©ÎãàÎã§.
    this.worker.onmessage = (event) => {
      const { type, landmarks } = event.data;
      if (type === "ready") {
        this._startAnalysisLoop();
      } else if (type === "result") {
        this._handleAnalysisResult(landmarks);
      }
    };
  }

  // ‚úÖ main.jsÏóêÏÑú AI Î™®ÎìàÏùÑ ÏãúÏûëÌïòÍ∏∞ ÏúÑÌï¥ Ìò∏Ï∂úÌïòÎäî Î©îÏÜåÎìúÏûÖÎãàÎã§.
  // Ïù¥Ï†ú Ïù¥ Î©îÏÜåÎìúÎäî ÎπÑÏñ¥ ÏûàÏñ¥ÎèÑ ÎêòÏßÄÎßå, Î™ÖÏãúÏ†ÅÏúºÎ°ú ÏãúÏûëÏ†êÏùÑ Í¥ÄÎ¶¨ÌïòÍ∏∞ ÏúÑÌï¥ ÎÇ®Í≤®Îë°ÎãàÎã§.
  // Ï§ëÏöîÌïú Ï†êÏùÄ onmessage Ìï∏Îì§Îü¨Í∞Ä Ïù¥ÎØ∏ ÏÉùÏÑ±ÏûêÏóêÏÑú ÏÑ§Ï†ïÎêòÏóàÎã§Îäî Í≤ÉÏûÖÎãàÎã§.
  start() {
    // console.log(
    //   "MediaPipeModule.start() called. Waiting for worker to be ready."
    // );
    // Ïã§Ï†ú ÏãúÏûë Î°úÏßÅÏùÄ workerÍ∞Ä 'ready' Î©îÏãúÏßÄÎ•º Î≥¥ÎÇº Îïå Ìä∏Î¶¨Í±∞Îê©ÎãàÎã§.
  }

  _startAnalysisLoop() {
    const AI_ANALYSIS_INTERVAL = 200;
    const analyzeFrame = async () => {
      // ‚úÖ ÎπÑÎîîÏò§ ÎÑàÎπÑ/ÎÜíÏù¥Í∞Ä 0Ïù¥ ÏïÑÎãàÍ≥†, ÏÉÅÌÉúÍ∞Ä Ï§ÄÎπÑÎêòÏóàÏùÑ ÎïåÎßå Î∂ÑÏÑù
      if (
        this.worker &&
        this.videoElement.readyState >= 2 &&
        this.videoElement.videoWidth > 0 &&
        this.videoElement.videoHeight > 0
      ) {
        try {
          const imageBitmap = await createImageBitmap(this.videoElement);
          this.worker.postMessage({ imageBitmap }, [imageBitmap]);
        } catch (error) {
          console.error(
            "‚ùå Error creating ImageBitmap in MediaPipeModule:",
            error
          );
        }
      }
      setTimeout(analyzeFrame, AI_ANALYSIS_INTERVAL);
    };
    setTimeout(analyzeFrame, AI_ANALYSIS_INTERVAL);
  }

  _handleAnalysisResult(landmarks) {
    // ‚úÖ 2. ÎûúÎìúÎßàÌÅ¨ Í∑∏Î¶¨Í∏∞ ÏöîÏ≤≠ÏùÄ Ïù¥Î≤§Ìä∏Î°úÎßå Î∞©ÏÜ°Ìï©ÎãàÎã§.
    this.emit("landmarksUpdate", landmarks);

    const previousIsPresent = this.isPresent;
    const previousIsDrowsy = this.isDrowsy;

    // --- ÏûêÎ¶¨ ÎπÑÏõÄ / Î≥µÍ∑Ä ÌåêÎã® ---
    if (!landmarks) {
      this.absenceCounter++;
      if (this.absenceCounter > this.ABSENCE_CONSECUTIVE_FRAMES) {
        this.isPresent = false;
      }
    } else {
      this.absenceCounter = 0;
      this.isPresent = true;
    }

    // --- Ï°∏Ïùå ÌåêÎã® (ÏñºÍµ¥Ïù¥ Í∞êÏßÄÎêú Í≤ΩÏö∞ÏóêÎßå) ---
    if (landmarks) {
      const getEAR = (eyeIndices) => {
        const pts = eyeIndices.map((i) => landmarks[i]);
        const d = (a, b) => Math.hypot(a.x - b.x, a.y - b.y);
        return (
          (d(pts[1], pts[5]) + d(pts[2], pts[4])) / (2 * d(pts[0], pts[3]))
        );
      };
      const ear = (getEAR(this.LEFT_EYE) + getEAR(this.RIGHT_EYE)) / 2;

      if (ear < this.EAR_THRESH) {
        this.closureFrames++;
        if (this.closureFrames >= this.DROWSY_FRAMES) {
          this.isDrowsy = true;
        }
      } else {
        this.isDrowsy = false;
        this.closureFrames = 0;
      }
    } else {
      // ÏñºÍµ¥Ïù¥ ÏóÜÏúºÎ©¥ Ï°∏Ïùå ÏÉÅÌÉúÎäî ÏïÑÎãàÎØÄÎ°ú Î¶¨ÏÖã
      this.isDrowsy = false;
      this.closureFrames = 0;
    }

    // ‚úÖ 3. ÏÉÅÌÉúÍ∞Ä 'Î≥ÄÍ≤Ω'ÎêòÏóàÏùÑ ÎïåÎßå Ïù¥Î≤§Ìä∏Î•º Î∞©ÏÜ°Ìï©ÎãàÎã§.
    if (previousIsPresent !== this.isPresent) {
      this.emit("absenceUpdate", { isPresent: this.isPresent });
    }
    if (previousIsDrowsy !== this.isDrowsy) {
      this.emit("drowsinessUpdate", { isDrowsy: this.isDrowsy });
    }
  }
}
